import { Col, Row, Spin } from 'antd';
import axios from 'axios';
import React, { useEffect, useState } from 'react';
import AlumniCard from '../UiComponents/Cards/AlumniCard';
import GreetingBar from '../UiComponents/GreetingBar';
import { UserAddOutlined, PlusOutlined } from '@ant-design/icons';
import AddAlumni from '../UiComponents/Modals/AddAlumni';
const ManageAlumni = () => {
  const [spin, setspin] = useState(false);
  const [data, setdata] = useState();
  const [addAlumni, setaddAlumni] = useState(false);
  useEffect(() => {
    setspin(true);
    axios
      .get(`https://ginnovation-server.herokuapp.com/api/alumni`)
      .then((resp) => {
        setspin(false);
        console.log(resp.data[0].alumniBatch);
        setdata(resp.data[0].alumniBatch);
      })
      .catch((err) => {
        setspin(false);
        console.log(err);
      });
  }, []);

  return (
    <div className='page manage-alumni'>
      {spin && (
        <div className='modal-container'>
          <Spin size='large' />
        </div>
      )}
      <GreetingBar />
      <div>
        {data &&
          data.map((value, index) => {
            return (
              <div>
                <h4 className='year'>
                  Alumni Batch of {value.batch - 4}-{value.batch}
                </h4>
                <Row gutter={12}>
                  {value.records.map((value, index) => {
                    return (
                      <Col md={6} key={index}>
                        <AlumniCard
                          imgSrc={value.profilePicture}
                          name={value.fullname}
                          headline={value.designation}
                          branch={value.branch}
                          about={value.about}
                          liLink={value.links[0].linkedin}
                          ghLink={value.links[0].github}
                          igLink={value.links[0].instagram}
                          fbLink={value.links[0].facebook}
                        />
                      </Col>
                    );
                  })}
                  <Col md={6}>
                    <div
                      className='add-card'
                      onClick={() => setaddAlumni(true)}
                    >
                      <UserAddOutlined className='icon' />
                    </div>
                  </Col>
                </Row>
              </div>
            );
          })}
      </div>
      <h5 className='year new-batch'>
        Add a New Alumni Batch <PlusOutlined className='icon' />
      </h5>
      {addAlumni && <AddAlumni handleClose={() => setaddAlumni(false)} />}
    </div>
  );
};

export default ManageAlumni;
