import React from 'react';
import fb from '../../../assets/Icons/facebook.png';
import li from '../../../assets/Icons/linkedin.png';
import gh from '../../../assets/Icons/github.png';
import ig from '../../../assets/Icons/instagram.png';

const AlumniCard = (props) => {
  const profileImg = {
    backgroundImage: `url(${props.imgSrc})`,
  };
  return (
    <div className='people-card alumni-card'>
      <div className='cover'></div>
      <div className='people-card-body'>
        <div className='profile-picture' style={profileImg}></div>
        <h5>{props.name}</h5>
        <p className='headline'>{props.headline}</p>
        <hr />
        <p className='branch'>{props.branch}</p>
        <hr />
        <div className='sm-container'>
          <a href={props.liLink} target='_blank' rel='noreferrer'>
            <img src={li} alt='alumni linkedin handle' />
          </a>
          <a href={props.igLink} target='_blank' rel='noreferrer'>
            <img src={ig} alt='alumni instagram handle' />
          </a>
          <a href={props.fbLink} target='_blank' rel='noreferrer'>
            <img src={fb} alt='alumni facebook handle' />
          </a>
          <a href={props.ghLink} target='_blank' rel='noreferrer'>
            <img src={gh} alt='alumni github handle' />
          </a>
        </div>
      </div>
    </div>
  );
};

export default AlumniCard;
